.container {
	width: 90%;
	margin: 5%;
	text-align: center;
	position: absolute;
}

.heading {
	color: #A0A0A0;
	margin-bottom: 7%;
}
.heading > h1 {
	color: #FFF;
}

.myworks {
	padding: 2rem;
}

.myworks > .row {
	display: grid;
	grid-template-columns: repeat(auto-fit, minmax(480px, 1fr));
	grid-gap: 1rem;
}

.myworks > .row > .col {
	margin: 1rem;
}

.myworks > .row > .col > a > img {
	width: 100%;
	border: 1px solid #E2E2E2;
	border-radius: .5rem;
}
.myworks > .row > .col > a > img:hover {
	opacity: .8;
	-moz-transition: all .2s ease-in;
	-o-transition: all .2s ease-in;
	-webkit-transition: all .2s ease-in;
	transition: all .2s ease-in;
}